import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';




@Injectable()
export class ConfigService {
    constructor(private http: HttpClient) { }
    configUrl = 'https://dgdq9nx784.execute-api.us-east-1.amazonaws.com/test/users';
    // configUrl = 'https://jsonplaceholder.typicode.com/posts/1';


    getConfig(): Observable<any> {
        // const httpOptions = {
        //     headers: new HttpHeaders({
        //         'Content-Type':  'application/json',
        //         'Authorization': 'my-auth-token'
        //     })
        // };

        // const params = new HttpParams().set('x-api-key', '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y');

        // httpOptions.headers =
        //     httpOptions.headers.set('Authorization', 'my-new-auth-token');


        const body: any = {
            'phone': 'stylist phone number',
            'firstName': 'name',
            'lastName': 'last name',
            'email': 'Personal email',
            'salonName': 'Hair',
            'salonEmail': 'john@email.com'
        };
        return this.http.get('https://dgdq9nx784.execute-api.us-east-1.amazonaws.com/test/users', {
            observe: 'body',
            responseType: 'json',
            headers: new HttpHeaders().append('x-api-key', '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y')
        });
    }


}





